<template>
  <section id="result">
        <loading :active.sync="isLoading" :can-cancel="true" :is-full-page="fullPage"></loading>
    <div class="container">
      <h3 class="result-text mt-3">SUCCESS</h3>
    </div>
    <div class="grey-bg">
      <div class="tab-content" id="myTabContent">
        
          <div class="d-flex justify-content-between table-wrap p-3 ">
            <table class="table table-borderless tables">
              <thead>
                <tr>
                  <th>Games</th>
									<th>Draw Number</th>
                  <th>Machine Number</th>
                  <th>Date & Time</th>
                </tr>
              </thead>
              <tbody>
                <tr class="row1" v-for="(result, index) in itemsSuccess.slice().reverse()" v-bind:key="index">
                  <td>{{result.name}}</td>
									<td><div class="card pl-2 redirect redirect2">
                <div
                  class="ball addmargin" 
                   v-for="draw_number in result.draw_numbers" :key="draw_number">
                  <div class="white-bg1 d-flex align-items-center justify-content-center">{{draw_number}}</div>
                  <img class="shadow1" src="/img/shadow.png">
                </div>
              </div></td>
                  <td><div class="card pl-2 redirect redirect2">
                <div
                  class="ball addmargin" 
                   v-for="machine_number in result.machine_numbers" :key="machine_number">
                  <div class="white-bg1 d-flex align-items-center justify-content-center">{{machine_number}}</div>
                  <img class="shadow1" src="/img/shadow.png">
                </div>
              </div></td>
                  <td>{{result.date}}</td>
                </tr>
              </tbody>
            </table>
          </div>
      </div>
    </div>
  </section>
</template>
<script>
import axios from 'axios';
import Loading from "vue-loading-overlay";
import "vue-loading-overlay/dist/vue-loading.css";

export default {
  components: { Loading },
 data(){
    return {  
      results: [],
      isLoading: false,
      fullPage: true
    }
  },
  mounted() {
    this.isLoading = true;
    this.getResult();
     // console.log(res)
      // simulate AJAX
      setTimeout(() => {
        this.isLoading = false;
      }, 5000);
  },

  methods: {
    getResult() {
      axios.get("https://middlewarez.herokuapp.com/api/v1/game/result?auth=55904993399349495bb6fdd&as=viewresult")
      .then((response) => {

       const winningNumber = response.data.data;
    const newArray = winningNumber.map(function (item) {
    return {
        draw_numbers: item.draw_numbers.split('-'),
        country: item.country,
        date: item.date,
        machine_numbers: item.machine_numbers.split('-'),
        name: item.name,
    };
});
this.results = newArray
console.log(newArray);
      }).catch( error => { console.log(error); });
    }
  },

  computed:{
  itemsSuccess: function() {
    return this.results.filter(function(item) {
      return item.name === "Success";
    })
  }
}
};
</script>
 
